.footer {
    background-color: #fff5f6;
    padding-bottom: 25px;

    @media screen and (min-width: 768px) {
        padding-bottom: 18px;
    }

    @media screen and (min-width: 1280px) {
        padding-bottom: 10px;
    }
}

.footer__block {
    margin-bottom: 43px;

    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 91px;
    }

    @media screen and (min-width: 1280px) {
        margin-bottom: 97px;
    }
}

.footer__slogan {
    margin-bottom: 17px;
    width: 232px;
    font-family: Titan One;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.145;
    text-transform: uppercase;
    color: #ffa5ba;

    @media screen and (min-width: 768px) {
        margin-bottom: 16px;
        width: 340px;
        font-size: 26px;
        letter-spacing: 0.04em;
    }

    @media screen and (min-width: 1280px) {
        margin-bottom: 15px;
        width: 477px;
        font-size: 30px;
        letter-spacing: 0.04em;
    }
}

.footer__slogan--last {
    margin-bottom: 43px;
    font-size: 34px;
    letter-spacing: 0.04em;
    color: $accentColor;

    @media screen and (min-width: 768px) {
        order: 1;
        margin-bottom: 0;
        font-size: 48px;
        letter-spacing: 0.06em;
    }

    @media screen and (min-width: 1280px) {
        font-size: 58px;
    }
}

.footer__soc-list {
    display: flex;
    height: 32px;
    justify-content: flex-end;
}

.footer__soc-list--top {
    margin-bottom: 46px;

    @media screen and (min-width: 768px) {
        margin-bottom: 0;
    }
}

.footer__soc-item {
    margin-right: 28px;

    @media screen and (min-width: 768px) {
        margin-right: 30px;
    }

    @media screen and (min-width: 1080px) {
        margin-right: 25px;
    }
}

.footer__soc-link {
    fill: #fea5bb;
    transition: fill $animation;

    &:focus,
    &:hover {
        fill: $accentColor;
    }
}

.footer__address {
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.01em;
    text-align: right;
    color: #000000;

    @media screen and (min-width: 768px) {
        order: 1;
        margin-top: 6px;
        font-size: 14px;
    }

    // margin-top compensates shortage of margin-bottom at class .footer__soc-link
    @media screen and (min-width: 1280px) {
        margin-top: 33px;
        letter-spacing: 0.02em;
    }
}

.footer__tel {
    color: $accentColor;
}

.footer__copyright {
    border-top: 1px solid #e1e1e1;
    padding: 22px 86px 0;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    color: #907e82;

    @media screen and (min-width: 768px) {
        padding-top: 29px;
        line-height: 1.86;
        letter-spacing: 0.02em;
    }

    @media screen and (min-width: 1280px) {
        padding-top: 9px;
    }
}